.MaterialFilter {
    margin-top: 70px;
    display: flex;
}
.MaterialFilter > div {
    padding: 10px 50px;
    border-radius: 20px;
    margin: 0 10px;
    cursor: pointer;
}
.ActiveFilter {
    background: #9577CB6E 0% 0% no-repeat padding-box;
    border: 2px solid #9577CB;
}
.Materials {
    display: flex;
    margin-top: 40px;
    flex-wrap: wrap;
}
.Material {
    width: 20%;
    background: #342654 no-repeat;
    border-radius: 10px;
    padding: 20px;
    margin: 20px;
    height: 270px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.Material > *:not(p) {
    background: #4F3F71 0% 0% no-repeat padding-box;
    border-radius: 7px;
    padding: 14px;
    text-align: center;
}
.Material > p {
    /* color: black; */
}
.MaterialType {
    align-self: flex-end;
}
.PreviousNext {
    display: flex;
    justify-content: center;
}
.PreviousNext > * {
    margin: 10px;
}
@media screen and (max-width: 800px) {
    .Material {
        width: 27%;
    }
}
@media screen and (max-width: 500px) {
    .Material {
        width: 100%;
        height: 300px;
    }
    .MaterialFilter > div {
        margin: 0 5px;
        padding: 5px 20px;
        margin: auto;
    }
}
.tooltip {
    position: relative;
    display: inline-block;
  }
  
  .tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: #555;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 50%;
    margin-left: -60px;
    opacity: 0;
    transition: opacity 0.3s;
  }
  
  .tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
  }
  
  .tooltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
  }