.Landing {
    width: 75%;
    margin: auto;
    margin-top: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.FinancialFreedomText {
    width: 35%;
}
.PhoneTrade {
    width: 55%;
}
.PhoneTrade > img {
    width: 100%;
}
.FinancialFreedomText > p {
    margin: 50px 0;
}
.Button > span {
    transition: .3s ease all;
}
.Body {
    background: #191529 0% 0% no-repeat padding-box;
}
.About {
    display: flex;
    width: 75%;
    margin: auto;
    padding-top: 60px;
}
.AboutContent {
    width: 35%;
    padding-top: 300px;
    margin-left: 80px;
    color: #E4E4E4;
}
.AboutHeader {
    display: flex;
    align-items: center;
    color: #E4E4E4;
}
.AboutLine {
    background: #E4E4E4;
    height: 2px;
    flex: 1;
    margin-left: 5px;
}
.AboutContent > * {
    color: #E4E4E4;
}
.AboutContent > h1 {
    margin-bottom: 50px;
}
.OurWays, .LearningContainer, .ExplainItemsContainer, .TestimonialContainer {
    background: #191529;
    padding-top: 150px;
    width: 100%;
}
.OurWays > div, .ExplainItems, .Testimonial {
    width: 75%;
    margin: auto;
    display: flex;
    justify-content: space-between;
}
.OurWays > div > div {
    width: 45%;
}
.OurWaysContent {
    display: flex;
    flex-direction: column;
    padding-top: 100px;
}
.ForexCryptoButtons {
    display: flex;
    justify-content: center;
    margin: 50px 0;
}
.ForexCryptoButtons > * {
    padding: 5px 50px;
    margin: 0 20px;
    cursor: pointer;
}
.Learning {
    align-items: flex-start;
    margin: 0 auto;
}
.LearningContent {
    padding-top: 50px;
    width: 45%;
}
.ExplainItems {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-column-gap: 50px;
    grid-row-gap: 40px;
}
.ExplainCard {
    padding: 40px 50px;
    width: 100%;
    background: transparent linear-gradient(142deg, #714361 0%, #777171 100%) 0% 0% no-repeat padding-box;
    border-radius: 16px; 
    display: flex;   
    justify-content: space-between;
    align-items: center;
}
.ExplainCard > div {
    width: 60px;
    height: 60px;
    border: 1px solid var(--unnamed-color-989898);
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #989898;
    opacity: 0.37;
    margin-right: 50px;
}
.ExplainCard > p {
    flex: 1;
    color: #E4E4E4;
}
.TestimonialContainer {
    background-image: url('../../assets/images/Group294.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background: url('../../assets/images/Group294.png') transparent linear-gradient(180deg, #191529 0%, #413D4EDB 52%, #65627000 100%) 0% 0% no-repeat padding-box;
}
.Testimonial {
    display: flex;
    justify-content: space-between;
}
.TestimonialLeft {
    width: 55%;
}
.TestimonialRight {
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.TestimonialRight > * {
    margin: 15px 0;
}
.ImageContainer {
    position: relative;
    overflow: hidden;
}
.Image {
    transition: .4s ease all;
}
.Play {
    position: absolute;
    top: calc(50% - 75px);
    left: calc(50% - 75px);
    width: 150px;
    height: 150px;
    cursor: pointer;
    margin: 0!important;
    transform: scale();
}
.HomeError {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.HomeError > h1 {
    margin-bottom: 100px;
}
@media screen and (max-width: 1025px) {
    .Landing, .About, .OurWays > div, .Testimonial {
        width: 95%;
    }
    .ExplainItems {
        width: 95%;
        grid-column-gap: 1%;
    }
}

@media screen and (max-width: 770px) {
    .ExplainItems {
        grid-template-columns: auto;
    }
}
@media screen and (max-width: 770px) {
    .Landing {
        flex-wrap: wrap;
        margin-top: 0px;
        flex-direction: column;
    }
    .Landing > div {
        width: 100%;
        margin: 10px 0;
    }
    .About {
        flex-direction: column-reverse;
    }
    .About > * {
        width: 100%!important;
        margin: 0;
        padding-top: 10px;
    }
    .AboutContent {
        margin: 5px 0;
    }
    .AboutContent > h1 {
        margin: 15px 0;
    }
    .OurWays, .OurWaysContent {
        padding-top: 20px;
    }
    .OurWays > div {
        flex-direction: column;
        padding-top: 20px;
    }
    .OurWays > div > div {
        width: 100%;
    }
    .LearningContainer {
        padding-top: 20px;
    }
    .ExplainItemsContainer {
        padding-top: 20px;
    }
    .ExplainCard {
        padding: 10px;
    }
    .ExplainItems {
        grid-row-gap: 5px;
    }
    .ExplainCard > div {
        margin-right: 10px;
    }
    .TestimonialContainer {
        padding-top: 20px;
    }
    .Testimonial {
        flex-direction: column;
    }
    .Testimonial > div {
        width: 100%;
        margin: 10px 0;
    }
    .TestimonialLeft > h1 {
        margin: 10px 0;
    }
}