.PhaseContainer {
    display: flex;
    margin-top: 20px;
}
.PhaseVideo {
    width: 65%;
    margin-right: 40px;
}
.CourseContent {
    flex: 1;
    margin-right: 40px;
}
.PhaseVideoDescription {
    background: #342654 0% 0% no-repeat padding-box;
    border-radius: 16px;
    opacity: 1;
    padding: 50px 40px;
    margin-top: 40px;
}
.PhaseVideoDescription > h3 {
    margin-bottom: 30px;
}
.CourseContentHeading {
    background: #342654 0% 0% no-repeat padding-box;
    border-radius: 16px;
    opacity: 1;
    padding: 20px;
    margin-bottom: 30px;
}
.CourseItems {
    padding: 60px 40px;
    background: #342654 0% 0% no-repeat padding-box;
    border-radius: 16px;
    height: 70vh;
    overflow-y: scroll;
}
.CourseItems > div {
    cursor: pointer;
}
.CourseItems::-webkit-scrollbar {
    width: 10px;
}

/* Track */
.CourseItems::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 5px;
}

/* Handle */
.CourseItems::-webkit-scrollbar-thumb {
    background: #9577CB 0% 0% no-repeat padding-box;
    border: 1px solid #9577CB;
    border-radius: 5px;
}

/* Handle on hover */
.CourseItems::-webkit-scrollbar-thumb:hover {
    background: #9577CB 0% 0% no-repeat padding-box;
    border: 1px solid #9577CB; 
}
.CourseItems > div {
    display: flex;
    border: 1px solid #6A5397;
    padding: 10px;
    margin: 15px 0;
}
.CourseItems > div > h3 {
    width: 40px;
}
@media screen and (max-width: 1024px) {
    .CourseContent {
        margin-right: 0;
    }
}
@media screen and (max-width: 770px) {
    .CourseItems {
        padding: 20px 10px;
    }
}
@media screen and (max-width: 500px) {
    .PhaseContainer {
        flex-direction: column;
    }
    .PhaseContainer > div {
        width: 100%;
    }
    .PhaseVideoDescription {
        flex-direction: column-reverse;
        padding: 15px;
        margin-top: 0;
    }
    .CourseContent {
        margin-top: 20px;
    }
}