.Notification {
    background: #342654;
    width: 60%;
    border-radius: 16px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.NotificationIcon {
    width: 50px;
    height: 50px;
    margin-right: 20px;
    background: white;
}
.Notification > p {
    flex: 1;
}
.Announcement {
    background: #342654;
    margin: 50px 0;
    border-radius: 16px;
    padding: 20px;
}
.NumItem {
    display: flex;
}
.Announcement > h3 {
    margin-bottom: 20px;
}
.NumItem {
    margin: 5px 0;
}
.Num {
    margin-right: 10px;
}
.NumItem > p {
    width: 60%;
}
.PhaseProgress {
    display: flex;
    justify-content: space-between;
}
.PhaseProgress > div {
    width: 30%;
    background: #342654;
    border-radius: 16px;
    padding: 20px;
}
.Progress {
    display: flex;
    height: 10px;
    border-radius: 5px;
    border: 1px solid #9577CB;
}
.Empty, .Full {
    height: 100%;
    border-radius: 5px;
}
.Full {
    background: #9577CB 0% 0% no-repeat padding-box;
    border: 1px solid #9577CB;
}
.Percentage {
    font-size: .8em;
    text-align: right;
    margin-top: -10px;
}
.PhaseProgress > div > h1 {
    text-align: center;
}
.PhaseProgress > div > *:not(.Percentage) {
    margin: 20px 0;
}
@media screen and (max-width: 500px) {
    .Notification {
        width: 100%;
    }
    .NumItem > p {
        flex: 1;
    }
    .PhaseProgress {
        flex-direction: column;
    }
    .PhaseProgress > div {
        width: 100%;
        margin: 20px 0;
    }
}