.FooterContainer {
    width: 85%;
    margin: auto;
    margin-top: 200px;
    padding-bottom: 100px;
}
.FooterNavItems {
    display: flex;
    justify-content: center;
}
.FooterNavItems > a {
    margin: 0 50px;
}
.FooterLogo {
    margin-top: 50px;
    margin-bottom: 100px;
    display: flex;
    justify-content: center;
}
.CopyRightSocials {
    display: flex;
    justify-content: space-between;
}
.CopyRightSocials > div {
    width: 33%;
}
.Socials {
    display: flex;
    justify-content: space-around;
}
.Socials > a {
    font-size: 2em;
}
.Privacy {
    display: flex;
    justify-content: space-around;
}
@media screen and (max-width: 1025px) {
    .FooterContainer {
        width: 98%;
    }
}
@media screen and (max-width: 1025px) {
    .FooterContainer {
        margin-top: 20px;
    }
    .FooterNavItems {
        display: none;
    }
    .CopyRightSocials {
        flex-direction: column;
    }
    .CopyRightSocials > div {
        width: 100%;
    }
    .CopyRight {
        text-align: center;
        margin: 10px 0;
    }
}
