.PioneersPageContainer {
    width: 75%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.PioneersPage {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.PioneersHeading {
    text-align: center;
    width: 50%;
    margin: 100px 0;
}
.LineHeader {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
}
.LHeader {
    color: #989898;
}
.LLine {
    margin-left: 10px;
    flex: 1;
    height: 3px;
    background-color: #989898;
}
.Leaders {
    display: flex;
    justify-content: space-between;
    margin-bottom: 100px;
    margin-top: 70px;
}
.LeaderCard {
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 30%;
    margin: 0;
}
.LeaderCard > h3 {
    margin: 20px 0;
}
.LeaderCard > p {
    margin: 0;
}
.LeaderCard > img {
    width: 100%;
}
.PioneersBody {
    width: 75%;
    margin: auto;
    margin-top: 200px;
}
.SuccessHeader {
    width: 75%;
    margin: auto;
    margin-bottom: 100px;
}
.SuccessHeader > h1 {
    width: 50%;
    margin-top: 50px;
}
.SuccessImages {
    display: flex;
    flex-wrap: wrap;
}
.SuccessImages > img {
    width: 250px;
    margin: 20px;
}
.GetStarted {
    width: 60%;
    margin: auto;
    text-align: center;
}
.GetStarted > p {
    margin: 50px 0;
}
@media screen and (max-width: 1024px) {
    .PioneersBody {
        width: 90%;
    }
    .SuccessHeader {
        width: 90%;
    }
}
@media screen and (max-width: 768px) {
    .GetStarted {
        width: 90%;
    }
}
@media screen and (max-width: 500px) {
    .Leaders {
        flex-direction: column;
    }
    .LeaderCard {
        width: 100%;
        margin-top: 40px;
    }
    .SuccessHeader > h1 {
        width: 100%;
    }
    .SuccessImages {
        justify-content: center;
    }
    .SuccessImages > img {
        width: 90%;
    }
}