.Err {
    align-items: center;
    width: 100%;
    background-image: linear-gradient(black, #22154DDB);
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.DashboardErr {
    align-items: center;
    width: 100%;
    background-image: linear-gradient(black, #22154DDB);
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.Err > div > h1, .DashboardErr > div > h1 {
    font-size: 10em;
}
.Err > div, .DashboardErr > div {
    display: flex;
    justify-content: center;
    align-items: center;
}
.Err > div:last-child {
    flex-direction: column;
}
.Err a {
    margin-top: 20px;
}
.DashboardErr > div:last-child {
    flex-direction: column;
}
.DashboardErr a {
    margin-top: 20px;
}
