.SignIn {
    margin-top: 0px;
    flex-direction: column;
    align-items: center;
}
form {
    text-align: left;
}
.NavigateLoginSignup {
    border: none;
    background: #9577CB 0% 0% no-repeat padding-box;
    /* border: 2px solid #9577CB; */
    width: 30%;
    margin-top: 30px;
    text-decoration: none;
    padding: 10px;
    border-radius: 4px;
}
button:disabled {
    opacity: .6;
    cursor: not-allowed;
}
.Error {
    color: tomato;
    font-size: .8em;
}
@media screen and (max-width: 1024px) {
    .SignInForm {
        width: 45%;
    }
    .NavigateLoginSignup {
        width: 45%;
    }
}
@media screen and (max-width: 780px) {
    .SignInForm {
        width: 55%;
    }
    .NavigateLoginSignup {
        width: 55%;
    }
}
@media screen and (max-width: 500px) {
    .SignInForm {
        width: 90%;
    }
    .NavigateLoginSignup {
        width: 90%;
    }
}