.DashboardSideNav {
    background: #342654;
    position: fixed;
    top: 0;
    left: 0;
    width: 20vw;
    height: 100vh;
    padding: 50px 20px;
    transition: .4s ease all;
    overflow-y: auto;
}
.DashboardSideNav > img {
    width: 50%;
}
.SideNavItems {
    background: #1A152C;
    border-radius: 14px;
    padding: 30px!important;
}
.ProfileImg {
    display: flex;
    align-items: center;
}
.ProfileImg > img {
    border-radius: 10%;
    margin: 0!important;
    margin-right: 10px!important;
}
.DashboardSideNav > div, .DashboardSideNav > div > a {
    padding: 10px 20px;
    margin: 10px 0;
}
.SideNavItems {
    display: flex;
    flex-direction: column;
    align-items: stretch;
}
.SideNavItem {
    cursor: pointer;
}
button.SideNavItem {
    background: inherit;
    border: none;
    /* align-self: stretch; */
    text-align: start;
}
.SideNavItem > i {
    margin-right: 20px;
}
.ActiveSideNav {
    background: #9577CB6E 0% 0% no-repeat padding-box;
    border: 2px solid #9577CB;
    border-radius: 24px;
}
.Logout {
    border: none;
    background: #342654 0% 0% no-repeat padding-box;
    border: 2px solid #9577CB;
    border-radius: 11px;
    opacity: 1;
    padding: 10px;
    width: 90%;
    position: absolute;
    bottom: 20px;
    left: 5%;
    margin: auto;
    cursor: pointer;
}
.Logout > i {
    margin-right: 20px;
}
@media screen and (min-width: 2559px) {
    .DashboardSideNav {
        width: 13%;
        left: 20%;
    }
}
@media screen and (min-width: 1024px) {
    .DashboardSideNav {
        transform: translateX(0)!important;
    }
}
@media screen and (min-width:800px) and (max-width: 1400px) {
    .DashboardSideNav {
        padding: 10px;
    }
    .SideNavItems {
        padding: 5px!important;
    }
}
@media screen and (max-width: 800px) {
    .DashboardSideNav {
        z-index: 100;
        width: 30%;
    }
}
@media screen and (max-width: 500px) {
    .DashboardSideNav {
        width: 75%;
        padding: 5px 20px;
    }
    .DashboardSideNav > img {
        display: none;
    }
    .SideNavItems {
        padding: 10px 20px!important;
    }
}