* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: #F5F5F5;
}
@media screen and (min-width: 1545px) {
  * {
    max-width: 1445px;
    margin: auto;
  }
  html {
    background: #22154DDB;
  }
}
html body {
  height: 100%;
}
.ImgBackground {
  background-image: #22154DDB;

  height: calc(100vh + 200px);

  /* Center and scale the image nicely */
  background-color: #22154DDB;
}
.ImgBackground > div {
  height: 100%;
  background: url('./assets/images/null.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}
p, * {
  font-family: Lato;
}
h1 {
  font-weight: normal;
  font-family: Maven Pro;
  font-size: 3em;
}
h2, h3, h4, h5 {
  font-family: Maven Pro;
  font-weight: normal;
}
.Button {
  padding: 5px 20px;
  background: #9577CB6E 0% 0% no-repeat padding-box;
  border: 2px solid #9577CB;
  border-radius: 3px;
  opacity: 1;
}
.NegativeButton {
  padding: 5px 20px;
  /* background: #9577CB6E 0% 0% no-repeat padding-box; */
  border: 2px solid #9577CB;
  border-radius: 3px;
  opacity: 1;
}
@media screen and (max-width: 1025px) {
    h1 {
      font-size: 2em;
    }
}
@media screen and (max-width: 500px) {
    h1 {
      font-size: 1.7em;
    }
    img {
      margin: 50px 0!important;
    }
}
