.TestimonialPage {
    width: 60%;
    margin: auto;
}
.TestimonialHeader {
    margin: 70px 0;
    width: 50%;
}
.TestimonialBody {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
}
.TestimonialVideo {
    margin: auto;
    overflow: hidden;
    margin: 50px 0;
    position: relative;
}
.TestimonialVideo{
    width: 100%;
}
.TestimonialAnalytics {
    position: relative;
}
.TestimonialAnalytics > div {
    position: absolute;
}
.Accuracy {
    top: 8%;
    left: 48%;
    font-size: 2.5em;
}
.Accuracy, div:first {
    font-weight: bold;
}
.Ppp {
    top: 68%;
    left: 16%;
    font-size: 2.5em;
}
.TradesPerWeek {
    top: 40%;
    left: 84%;
    font-size: 2.5em;
}
.TestimonialAnalytics .Text {
    font-size: .47em;
}
.TestimonialAnalytics > img {
    width: 100%;
}
@media screen and (max-width: 1024px) {
    .TestimonialPage, .TestimonialHeader, .TestimonialBody {
        width: 75%;
    }
    .Accuracy, .Ppp, .TradesPerWeek {
        font-size: 1.8em;
    }
}
@media screen and (max-width: 500px) {
    .TestimonialPage, .TestimonialHeader, .TestimonialBody {
        width: 90%;
    }
    .TestimonialVideo {
        margin: 20px 0;
    }
    .Accuracy, .Ppp, .TradesPerWeek {
        font-size: 1em;
    }
    .Accuracy {
        top: 20%;
    }
    .Ppp {
        top: 60%;
    }
}