.TrainingContainer {
    margin-top: 40px;
    display: flex;
}
.TrainingList {
    width: 35%;
}
.TrainingItems {
    padding: 20px 5px;
    margin-right: 40px;
}
.TrainingVideo > .Image {
    flex: 1;
}
@media screen and (max-width: 1338px) {
    .TrainingList {
        width: 35%;
    }
}
@media screen and (max-width: 800px) {
    .TrainingContainer {
        flex-direction: column-reverse;
    }
    .TrainingItems {
        margin-right: 0;
        margin-top: 40px;
    }
}