.Dashboard {
    background-image: url('../../assets/images/null.png');
    background-repeat: no-repeat;
    background-attachment: fixed;
    /* width: 100vw;
    height: 100vh; */
    background-color: #22154dc0;
    min-height: 100vh;
}
.Content {
    margin-left: 20vw;
    width: 78vw;
    padding-left: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
}
.DashboardMenu {
    width: 50px;
    height: 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    z-index: 100;
    position: fixed;
    top: 20px;
    left: 20px;
    transition: .3s ease all;
    /* transform: translateX(0); */
}
.DashboardMenu > div {
    height: 3px;
    width: 100%;
    background: #E4E4E4;
}
.DashboardMenuTranslate {
    transform: translateX(30vw);
}
.DashboardMenuTranslate > div {
    position: absolute;
    transition: .3s ease all;
}
.DashboardMenuTranslate > div:nth-child(2) {
    width: 0;
    height: 0;
}
.DashboardMenuTranslate > div:nth-child(1) {
    transform: rotateZ(45deg);
}
.DashboardMenuTranslate > div:nth-child(3) {
    transform: rotateZ(-45deg);
}

.Cancel {
    display: block;
}
.Sticky {
    position: sticky;
}
@media screen and (min-width: 2559px) {
    .Content {
        width: 78%;
        margin-left: 20%;
    }
}
@media screen and (min-width: 1024px) {
    .DashboardMenu {
        display: none;
    }
}
@media screen and (max-width: 840px) {
    .Content {
        width: 100%;
        margin-left: 0;
        padding-right: 20px;
    }
}
@media screen and (max-width: 500px) {
    .DashboardMenuTranslate {
        transform: translateX(75vw);
    }
}
