.Header {
    display: flex;
    width: 85%;
    height: 160px;
    align-items: center;
    margin: auto;
    justify-content: space-between;
}
.Logo > img {
    height: 50px!important;
}
.NavItems > a, .SignIn > a {
    color: #ffffff;
}
.NavItems {
    width: 50%;
    display: flex;
    justify-content: space-around;
}
.Menu {
    display: none;
}
@media screen and (max-width: 1025px) {
    .Header {
        width: 95%;
    }
}
@media screen and (max-width: 500px) {
    .Header {
        justify-content: space-between;
    }
    .Logo {
        flex: 1;
        display: flex;
        justify-content: center;
    }
    .NavItems, .SignIn {
        display: none;
    }
    .Menu {
        width: 40px;
        height: 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .Menu > div {
        width: 100%;
        height: 3px;
        background: #E4E4E4;
    }
}