.VideoPlayerBackdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.87);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}
.VideoPlayerBackdrop > video {
    width: 70%;
    height: 80%;
    z-index: 1001;
}
@media screen and (max-width: 1366px) {
    .VideoPlayerBackdrop > video {
        height: 60%;
    }
}
@media screen and (max-width: 800px) {
    .VideoPlayerBackdrop > video {
        width: 100%;
    }
}
@media screen and (max-width: 500px) {
    .VideoPlayerBackdrop > video {
        height: 30%;
    }
}