.GetStartedHeader {
    margin: 200px 0;
    text-align: center;
    display: flex;
    justify-content: center;
}
.HowToStart {
    width: 50%;
    margin: auto;
    text-align: center;
}
.HowToStart > p {
    padding: 50px 0;
}
.JoinWebinars {
    width: 80%;
    margin: auto;
}
.JoinWebinars > h1 {
    margin: 50px 0;
    text-align: center;
}
.JoinWebinars > p {
    text-align: center;
}
.JoinWebinars > img {
    width: 100%;
}
form {
    width: 30%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-content: stretch;
    background-color: #191529;
    box-shadow: 0px 8px 20px #3754AA12;
    border-radius: 8px;
    padding: 30px;
}
form > input, form > select, form > input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active {
    background: #2E2C40 0% 0% no-repeat padding-box!important;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
}
form > p {
    margin: 10px 0;
}
form > h4 {
    font-size: 1.5em;
    margin-bottom: 40px;
}
form > button {
    background: #9577CB 0% 0% no-repeat padding-box;
    border: none;
    border-radius: 4px;
    margin-top: 20px;
    padding: 10px;
    cursor: pointer;
}
.ForgotPassword {
    text-align: right;
    margin: 20px 0;
}
@media screen and (max-width: 800px) {
    form {
        width: 50%;
    }
}
@media screen and (max-width: 500px) {
    form {
        width: 80%;
    }
}